module.exports={"en-US":{"Pp":"**CAPITUAL PRIVACY POLICY, DATA STATEMENT AND COOKIE CONSENT FOR CAPITUAL LTD, CAPITUAL.COM WEBSITE AND CAPITUAL APPLICATION PROGRAMMING INTERFACE**\n\n**LAST UPDATED** MARCH 27TH 2019\n\nThis document rules how Capitual Ltd., corresponding and partners collect, use and shares your personal data received from you while you browse on Capitual website, mobile application or use Capitual API-powered applications and services. Hereinafter, the mentioned methods to access Capitual will be referred to as “PLATFORM”.\n\nIt’s important to make it clear that, in order to make the PLATFORM available, tasks performed by a variable number of companies is required. These companies may or may not be part of Capitual Group. All these companies have signed a non-disclosure agreement with Capitual Group and do follow this Privacy Policy while managing your data.\n\n**1. COLLECTED INFORMATION**\n\nWhile you use the PLATFORM, we collect information from and about you, from different sources. Part of the collected data may be provided by your environment (browser, computer, smartphone etc.); part of the collected data may come from external services and partners; part of the collected data, you are requested to provide.\n\n1.1. Our Account Confirmation procedure requires you to provide personal documents, including pictures. This information will be used to verify your identity and protect our users against fraud.\n\n1.1.1. Personal Documents may be shared with third-parties, including, but not limited to law enforcement agencies, in case of suspicion of involvement with illegal activities.\n\n1.2. Your profile settings require additional data, such as address and full name. This information is used, in conjunction with the data collected related in 1.1, during fraud investigations. This information may also be used during support procedures.\n\n1.3. We collect your IP address and we share your IP address, without any information that could relate the IP address to you, with third-party services that provide IP location. Such services provide us with your approximated geolocation coordinates (latitude and longitude) and political divisions (city, state, country, time zone etc.). This data is used to protect your account from unauthorized access.\n\n1.4. Every time you access the PLATFORM, we generate a unique identifier based on your environment (the “fingerprint”). This information is used to recognize your machine and protect your account against unauthorized access. This information may also be used during investigations.\n\n1.5. We may also collect your browsing patterns and log your navigation, in order to improve your experience on the PLATFORM.\n\n1.6. We collect and store information provided by your environment (browser or smartphone), which includes, but is not limited to user agent, browser and operating system version, screen resolution, language, time zone, CPU architecture and manufacturer. This information is used to improve your experience on the PLATFORM.\n\n1.7. We collect email addresses and phone numbers you provide. This contact information may be shared in case of suspicion of involvement with illegal activities with law enforcement agencies.\n\n**2. USAGE OF THE COLLECTED INFORMATION**\n\nThe collected information is used in one or more of the following manners:\n\n2.1. To properly offer the service. The collected data is essential for us to provide you with the features your Capitual account has access to.\n\n2.2. To improve your experience. We use analytic measures for a better understanding of our user’s behavior on our platform.\n\n2.3. To verify your identity. We use provided details as part of our Know-Your-Customer and Anti-Money Laundering procedures.\n\n2.4. To emit communications. We may send conditional communications to part of our users.\n\n2.5. Investigations. We analyze, through automated models and human verification, your account activity to protect our users against fraud, illegal activities or involvement with any of the Prohibited Uses and Businesses (available at <http://capgo.gq/prohibited-businesses>).\n\n2.6. Operations with service providers and partners. We may use your personal data for performing our partner’s KYC/AML procedures and other possible requirements.\n\n2.7. Third-party applications. If you give a Capitual API-powered application access to your account, we will share detailed information about your account and personal information, including the ability to take actions on your behalf. Their usage of your data is subject to their terms and privacy policy.\n\n**3. PROTECTING THE COLLECTED INFORMATION**\n\nWe maintain internal security measures to protect your personal information against unauthorized access, accidental change or loss.\n\n3.1. We maintain your personal information physically safe by safeguarding servers where we store user’s information and controlling access to it, allowing only those who need such data to fulfill their responsibilities.\n\n3.2. We maintain your personal information digitally safe by following security and network management standards, such as strong encryption (AES-256) of your data while travelling internally or to authorized third-parties, keeping network firewalls and intrusion detection systems.\n\n3.3. We limit access to your personal information to employees and third-parties, allowing their access only to the information set they need to have access to. These individuals and organizations have signed non-disclosure agreements with Capitual Ltd and are required to keep all the information confidential.\n\n3.4. Note that there are still security risks, as no organization can genuinely guarantee the lack of such risk. Bad actors may bypass even the most advanced security system.\n\n**4. SHARING OF THE COLLECTED INFORMATION**\n\nWe may share your information internally, between Capitual agents, or externally.\n\n4.1. We may share your data with service providers and partners, including, but not limited to banking institutions and payment services providers.\n\n4.2. We may share your data with law enforcement agencies, when requested by a court or if we suspect you are involved in illegal activities.\n\n4.3. We do not sell, trade or transfer your personal data to third-parties who are not involved in the process of keeping the feature set that you have access to, nor to those who have not signed a non-disclosure agreement with Capitual Ltd. In most cases, our partners have a limited access to your information, and in several situations, you are only referred to as a number or other non-personal identification.\n\n4.4. We may share a limited set of your personal data, including, but not limited to your browsing activities, environment (browser, operating system and device) information and IP address with analytic services providers.\n\n4.5. We may share your data in the event of transfer of ownership of Capitual Ltd or its corresponding companies, that may or may not be part of Capitual Group.\n\n4.6. We may share impersonal details about Capitual users as a whole, including, but not limited to the total transactions value on a certain timespan, number of users and our obligations. In this case, the provided data is never, in any way, related to any individual or organization.\n\n**5. JURISDICTION-SPECIFIC INQUIRIES**\n\n5.1. For European Union residents. We may share your personal data to parties outside European Union, including the United States. These parties include, but are not limited to analytic services providers, banking institutions and advertising agencies.\n\n5.1.1. By registering to a Capitual account, you consent  that we may transfer your data to third-parties, according to the chapter 4.\n\n5.1.2. According to the General Data Protection Regulation, Capitual Ltd is the controller and responsible for the PLATFORM and any personal information provided to the PLATFORM. In some situations, Capitual Ltd may act as data processor for third-parties’ entities that may have access to your personal information, as ruled by the chapter 4.\n\n5.2. For Brazil residents. According to the Law 12965/14, Article 7th X, you may request the total deletion of the data we have collected about you through email to <privacy@capitual.net>. In this case, we need of 30 (thirty) days to completely remove the information we have collected about you from the data storage systems of the companies of Capitual Group.\n\n**6. YOUR RIGHTS**\n\nYou are granted the rights, through sending email to <privacy@capitual.net>, to:\n\n6.1. Request a copy of the information we’ve collected about you.\n\n6.2. Request correction in incorrect data about you.\n\n6.3. Stop receiving our marketing communications.\n\n**7. RETAINING YOUR PERSONAL INFORMATION**\n\n7.1. We retain your Personal Information for as long as we need to perform the purposes the data was collected to.\n\n7.2. As soon as a specified information is no longer needed, it is securely removed from our records.\n\n7.3. If you want to stop receiving our market communications, we retain your contact details to make it possible to comply such request.\n\n7.4. In some situations when your data is accessed, it is done in an anonymized way, following 4.3.\n\n7.5. In some jurisdictions, we are required to store your Personal Information for a longer time, which is usually from some months to some years, after you request your account’s termination.\n\n**8. ASPECTS REGARDING CHILDREN PRIVACY**\n\n8.1. We do not collect or solicit information from minors (children under 16 years old).\n\n8.2. We do not allow individuals under 16 years old has registered to a Capitual account.\n\n8.2.1. As soon as it’s known that a specified account is being operated by a child under 16 years old, the account will be suspended.\n\n8.2.2. Accounts suspended due to the age of the owner may be requested to be reactivated as soon as the owner reaches 16 years old.\n\n**9. COOKIES POLICY**\n\n9.1. Cookies are small files that are placed on your device by the website or application you are using. Pixel tags, clear GIFs, pixels or web beacons are small blocks of code on a webpage or app that allow them to do things like reading and placing cookies and transmitting information to the PLATFORM or partners. Local Storage and Session Storage are technologies that provide access to a data storage on your device.\n\n9.2. We use these technologies for several reasons, which include the basic functionality of the PLATFORM, session keeping (logged-on state), service improvement and to help us to maintain your account’s security.\n\n9.3. We use third-party service providers that may make use of these technologies in order to provide analytical data, debugging information and performance data.\n\n9.4. We may store data in your mobile device when you make use of Capitual Mobile Application or Capitual Website.\n\n9.4.1. The stored information’s purpose is also included on 9.2.\n\n9.5. Like most websites, we may set cookies as soon as you start interacting with our platform.\n\n9.5.1. Your web browser sends cookies for a particular domain every time it connects to that domain. This means that we are able to read a specified cookie every time a new connection is made against one of our servers.\n\n9.5.2. As of Local Storage and Session Storage, your web browser does not send such data over the network, but we may set, capture and transmit it over the network, to our servers, at any time while you are navigating on the PLATFORM.\n\n9.6. Although we require that developers using our API be transparent on their usage of cookies and storage, your usage of third-parties’ applications that use Capitual API is entirely subject to their Privacy Policies.\n\n9.7. We may still set cookies on your browser even if you are not a Capitual member or you are not logged in.\n\n9.7.1. These cookies may be used by analytic services providers, in order to improve users’ experience on the PLATFORM.\n\n9.8. You may disable cookies on your browser, but the usage of the PLATFORM may be affected.\n\n**10. CHANGES TO THIS PRIVACY POLICY**\n\nWe may change this privacy policy at any time, without prior notification. The most up-to-date privacy policy may be obtained by emailing <legal@capitual.net>.\n\n**11. PRIVACY CONTACT**\n\nYou may send any privacy-related inquiries to us by sending an email to <privacy@capitual.net>.\n\nIf you reside in the European Union and is not satisfied with the resolution of your complaint, you may contact EU Data Protection Authority in your jurisdiction using the contact details provided at [http://ec.europa.eu/justice/article-29/structure/data-protection-authoritiesindex_en.htm](http://ec.europa.eu/justice/article-29/structure/data-protection-authoritiesindex_en.htm) for further assistance.\n","Tos":"\n**USER AGREEMENT FOR CAPITUAL LTD SERVICES, CAPITUAL.COM WEBSITE, CAPITUAL MOBILE APPLICATION AND THIRD-PARTIES’ APPLICATIONS POWERED BY CAPITUAL**\n\nLAST UPDATED March 26th 2019\n\n**1. INTRODUCTION**\n\n1.1. The present User Agreement, hereinafter referred to as TERMS, THE TERMS or TERMS OF USE, rules the relation between Capitual Ltd., a company under the laws of Malta, registered under the number C 90611, authorised by Malta Financial Services Authority, with registered office at Level 4, The Penthouse, Suite 2, Ewropa Business Centre, Triq Dun Karm, Birkirkara BKR 9034, Malta, hereinafter referred to as CAPITUAL, PLATFORM or THE PLATFORM, WE, OUR or US; and customers willing to make use of CAPITUAL services, hereinafter referred to as CUSTOMER, CLIENT, YOU or YOUR.\n\n1.2. Contracting Parties. This agreement represents a contract between the CLIENT and the PLATFORM, as well as, when applicable, between the CLIENT and other services providers integrated with CAPITUAL. Denying or lacking to follow one or more conditions on the TERMS may lead to the termination of the CUSTOMER’S CAPITUAL account and, if applicable, to further legal complications, under the terms of the law.\n\n1.2.1. Governing Law. You agree that the laws of the Republic of Malta, without regards to principles of conflicting laws, govern the current agreement between the two parties and must rule any claim or dispute that may result from the business relationship this document covers.\n\n1.3. This agreement is nontransferable. Any attempt to transfer the present agreement will be voided and represents breach of the current terms.\n\n1.4. CAPITUAL reserves the right to modify, at any time, the present TERMS, with no previous notification. Your continued use of the PLATFORM is subject to the most updated TERMS, which is always available at CAPITUAL’s website and may also be requested by email to <[legal@capitual.net](mailto:legal@capitual.net)>.\n\n1.5. If you are registering on the behalf of an organization, you are confirming that you have legal authorisation to accept agreements on the behalf of your organization.\n\n1.6. If the CUSTOMER does not agree with one or more terms or conditions on the present TERMS or PRIVACY POLICY, then the CUSTOMER is not given the rights to sign up for a CAPITUAL account and shall not make use of any CAPITUAL-powered services.\n\n**1.7. THE CUSTOMER ACKNOWLEDGES THE IMPORTANCE OF THE PRESENT TERMS AND HAVE CONSIDERED EACH TERM CAREFULLY WHEN OPTING TO BECOME A MEMBER OF THE SERVICE. THIS DOCUMENT CONTAINS PROVISIONS THAT DEFINE THE POSSIBILITIES AND LIMITATIONS FOR CLAIMS THAT THE PLATFORM AND THE CUSTOMER MAY HAVE TO EACH OTHER. CAPITUAL’S LIABILITY TO THE CUSTOMER HAS LIMITATIONS THAT ARE IMPOSED BY THE PRESENT AGREEMENT.**\n\n**2. PROVISIONS THE CUSTOMER MAKE TO US**\n\nYou confirm and warrant to us that the following statements are and will remain true while you are a CAPITUAL user:\n\n2.1. You are at least 18 years old.\n\n2.2. You are able to agree with the current terms.\n\n**COMMUNICATIONS**\n\n2.2.1. You consent to receive and read all official communications issued by Capitual, including, but not limited to:\n\n - Transaction receipts or rejections;\n\n - Changes regarding the present TERMS, the PRIVACY POLICY or any other official document;\n\n - Changes regarding the PLATFORM and maintenance information;\n\n - Requesting information about you or your account.\n\n2.2.2. You agree to reply, when applicable, to these communications.\n\n2.2.3. You agree to keep your primary email updated (this setting is available on your profile page) and check your spam box for false-positives involving Capitual communications.\n\n2.2.4. You will check if the received information is officially issued by Capitual, and not a third trying to impersonate Capitual, through:\n\n - The receiving email address must be your primary address;\n\n - The email shall not have links to non-Capitual domains or subdomains (see 2.8.4) requesting your private information;\n\n - Public disclosure relevant questions must also be available in our pressroom, available at <[https://press.capitual.com](https://press.capitual.com)>;\n\n - You can always forward an email to [support@capitual.com](mailto:support@capitual.com) if you want to verify its origin.\n\n2.2.5. You can, at any time and at no costs, request another electronic copy of any communication, in case of accidental loss, by sending an email to <[support@capitual.com](mailto:support@capitual.com)>.\n\n2.3. The law of the jurisdiction where you reside does allow you to make use of all CAPITUAL services.\n\n2.4. You are not engaged and will not engage in any Prohibited Business.\n\n**PERSONAL DOCUMENTS AND PROOF-OF-LIFE**\n\n2.5. You will provide us with your official identity issued by your local government (identity card or driver’s license), or a valid passport, and you agree that you will provide new documents as soon as the provided documents become invalid or re-issued.\n\n2.5.1. When requested, you will provide additional personal information and additional documents, including, but not limited to, legal name, address, date of birth, bank information, taxpayer identification number, proof-of-address and personal pictures (“selfies”).\n\n2.5.2. You agree to provide us with face identification and tracking data, which will be used to protect YOU and THE PLATFORM.\n\n2.5.3. You will also performed, when required, physical actions, through our automated alive-person verification system or through video-call with an authorised agent.\n\n2.5.4. You agree that your collected personal data may be shared with third-parties, when requested, including, but not limited to, Law Enforcement Agencies and banks.\n\n2.5.5. You agree that you will maintain your contact information (emails and phone numbers) updated, and confirm ownership of any contact method when requested. This is done through a link or an instant token sent by email or SMS.\n\n2.5.6. Our documents requesting procedures are part of our Know-Your-Customer and Anti-Money Laundering Policy, which can be viewed at: <http://capgo.gq/kycaml>.\n\n**SANCTIONS**\n\n2.6. You agree to have your documents and personal data checked against sanctions lists such as OFAC’s Specially Designated Nationals List, the U.S. Commerce Department’s Denied Persons List, HM Treasury’s financial sanctions regime and Consolidated United Nations Security Council Sanctions List, on a daily basis.\n\n2.7. You will not supply any product or service to a resident of jurisdictions subject to Malta or European Union embargos, available at <[http://capgo.gq/maltese-sanctions](http://capgo.gq/maltese-sanctions)> and in official sources.\n\n**INFORMATION SECURITY**\n\n2.8. You will not use your CAPITUAL account to practice illegal activities against the society, organisations or individuals. You will not access other’s CAPITUAL account, send communications (emails, SMS or any other form of communication) that look similar to CAPITUAL communications or may lead the readers to believe it is an official CAPITUAL communication.\n\n2.8.1. You will not develop software, scripts, browser extensions or apps that steal others’ CAPITUAL accounts, funds, password, PIN code or personal data.\n\n2.8.2. You will not do any kind of security testing, such as penetration testing, port scanning, services information gathering or exploiting against any CAPITUAL server, without explicit authorisation issued by CAPITUAL.\n\n2.8.3. You will not take any action that could make CAPITUAL services unavailable or look different to one or more users, including, but not limited to, denial of service or distributed denial of service attacks.\n\n2.8.4. If you find any vulnerability in our systems, you will immediately contact <[security@capitual.net](mailto:security@capitual.net)> or <[bugbounty@capitual.net](mailto:bugbounty@capitual.net)>, providing a proof-of-concept (“PoC”), using our test net environment (accessible through <[https://my.testnet.capitual.com](https://my.testnet.capitual.com)>) always when possible and optionally joining our bug bounty program. You cannot publicly disclose any detail about such vulnerability before explicit authorisation nor attack any main net account, even only to demonstrate the discovered vulnerability.\n\n2.8.5. You will not try to reverse any transaction sent to CAPITUAL. You will not chargeback such transactions on your bank nor attempt into double-spending attacks against CAPITUAL or any crypto-currency supported by CAPITUAL.\n\n2.8.6. You will help in maintaining your CAPITUAL account secure, which involves tasks such as, but not limited to:\n\n - Using a secure, strong password;\n\n - Changing your password on a regular basis;\n\n - Not keeping your account logged on in machines you are not using;\n\n - Not taking copies of your password, PIN code or credentials to your primary email address, nor giving anyone else’s access to such information;\n\n - Not taking copies of e-voucher claim codes, nor giving public access to it, as e-voucher claiming generates a transaction that is treated similar to any other transaction, under the current terms, and specially about its irreversibility;\n\n - Not giving anyone access to your phone, even locked, as your phone or SIM card may be used to reset your password and/or PIN code;\n\n - Keeping your machine safe, with a decent and updated antivirus software and firewall;\n\n - Avoiding fake pages, by always verifying if the page that claims to be from Capitual is the main domain or a subdomain of “[capitual.com](http://capitual.com)\", “[capitual.net](http://capitual.net)\", “[capitual.io](http://capitual.io)\", “capitu.al” or “capgo.gq”. In order to check if you are in one of these domains, please verify that, in the URL you are in, there is nothing or a slash (“/“) followed by the URI path after one of the mentioned domains;\n\n - Not giving secret information or your account’s credentials to anyone, by email, SMS, phone call or any other means, even those who claim to be from Capitual. Our agents have enough information to provide support in our internal applications, and no account information must be requested from the user;\n\n - Avoiding to enter your credentials (mainly your PIN code) in public networks or machines, such as cyber-cafes, company’s, airport’s, restaurants' or hotel’s networks;\n\n - Using login codes, issued in your account security settings, instead of the two-factors authentication code, when accessing your account in unknown networks, as the tokens may only be used once;\n\n - Enabling two-factors authentication;\n\n - Not pasting any code on your browser’s developers console or navigation bar;\n\n - Avoiding clicking unknown links or downloading unknown attachments, even those from known origins;\n\n - Not giving login codes or session identifiers to anyone.\n\n2.8.7. You are fully responsible for the state of your account, as well as credentials and access to it.\n\n2.8.8. You are fully responsible for all transactions performed on behalf of your account. Only you are responsible for any migration or loss or funds, including resulting from authorised access. CAPITUAL assumes no responsibility for any losses resulting from your account being compromised.\n\n2.9. You are granted a limited, non-exclusive, non-transferable and non-sublicensable license to access and use CAPITUAL servers, including our APIs. You agree that you have no rights beyond this limited license.\n\n2.9.1. You will not copy, transmit, distribute, sell, resell, re-license, de-compile, intercept, reverse engineer, disassemble, modify, patch, publish, participate in the creation, transfer or sale of, create derivative works from, perform, display, incorporate into another website (except through the API), or in any other way exploit the contents of CAPITUAL services, in parts or as a whole.\n\n2.9.2. You will not frame, display, broadcast or stream any part of the SERVICE or our trademarks without prior explicit permissions that may be requested by sending an email to <legal@capitual.net>.\n\n2.9.3. Authorized streams of our trademarks are ruled by our branding standards, that may be requested, after getting explicit permissions, by sending an email to <legal@capitual.net>.\n\n**ACCOUNT UNIQUENESS**\n\n2.9. You agree that you will only maintain one CAPITUAL account. You may request the right to have an additional account by sending an email to <[support@capitual.io](mailto:support@capitual.io)> in order to get a written permission from CAPITUAL to maintain a second account, but we have no obligation of approving any additional account.\n\n2.9.1. Having multiple accounts with no authorisation by CAPITUAL may lead to terminate the involved accounts.\n\n2.9.2. A CAPITUAL account is not subject to transfer to any person or organisation.\n\n**ACCOUNT AVAILABILITY**\n\n2.10. You confirm that you possess or have access to the minimum required hardware and software setup to access your CAPITUAL account, which is, as of now, but may be changed in the future:\n\n - A working computer, with a bootable operating system installed and correctly working;\n\n - A broad brand internet connection;\n\n - An updated web browser, such as Google Chrome or Mozilla Firefox, with cookies and JavaScript enabled;\n\n - A valid email address;\n\n - Sufficient storage space for the required cookies and cached information (at least 20 megabytes, 50 megabytes recommended); and, but optionally,\n\n - A smartphone running recent versions of Android or iOS, in order to run Capitual Mobile Application.\n\n**3. PROVISIONS OF THE PLATFORM**\n\n3.1. CAPITUAL provides the CUSTOMER with a CAPITUAL ACCOUNT, which is a personal area with functions based on the user’s personal data.\n\n3.1.1. A CAPITUAL ACCOUNT holds different forms of funds, such as crypto-currencies, precious metals or internal tokens, that may be exchange to and from fiat and crypto-currencies on the PLATFORM.\n\n**ASSETS AS TOKENS**\n\n3.1.2. The PLATFORM provides spaces in the form of “wallets”, hereinafter referred to as \"CAPWALLETS\".\n\n3.1.3. CAPWALLETS hold assets (“tokens”) that are financially equivalent to the assets you place in the PLATFORM.\n\n3.1.3.1. Your rights are exclusively on the value of the tokens you hold on the PLATFORM in any of the supported withdrawing methods for your jurisdiction.\n\n3.1.4. Assets placed at the PLATFORM will be transferred to CAPITUAL or one of the authorised correspondent companies around the world.\n\n3.1.5. For placing assets at the PLATFORM, you’ll be given the economic value of the assets in the form of tokens, in the CAPWALLET of your choice. The value of your tokens may be displayed, in real time, in the supported virtual currency (hereinafter referred to as “CURRENCY\") of your choice, and may also be exchanged, in parts or entirely, to any supported currency.\n\n3.1.6. In the event of your account being considered inactive (i.e. by no usage of the PLATFORM for more than 180 days or the time set by your state), CAPITUAL may be required to report your held funds as unclaimed property to the government of the jurisdiction your account is registered in.\n\n3.1.6.1. Beforehand, CAPITUAL will attempt several methods to contact you, which includes, but does not limit itself to email, phone or physical address. These attempts will take place some days before the 90 days timespan that your account did not receive any access. If, however, no contact attempt achieve success, your held funds may be surrendered to the jurisdiction.\n\n3.1.6.2. An account may be flagged as inactive if no access has been registered on it after 180 days (or the time set by your state). An access is considered using a previously-created session or starting a new session.\n\n3.1.6.3. If your account is terminated, you may request us to send your remaining balance by mail. In this case, it will be mailed to the address set in your account’s settings.\n\n3.1.6.4. If your funds had been locked, totally or partially, by law enforcement agencies, we are unable to attend such request.\n\n3.1.7. We rely on third-parties for storing part of the funds and providing the currency exchange services. As the whole crypto market is new, some of these parties do not have a long operating history and therefore their management is unproven. Since crypto market and financial services history have some examples of lost funds due to fraud, theft or other reasons, you accept the risk that one of more of these parties may not perform strictly following our agreements with them.\n\n**TRANSFERS AND EXCHANGE**\n\n3.2. You may move funds from one CAPWALLET to another one, as long as the origin CAPWALLET is under your ownership or you reserve the rights to spend it. This act results in a TRANSACTION (transaction deducting the value from the origin wallet) and a COLLATERAL TRANSACTION (transaction adding the value to the destination wallet).\n\n3.2.1. Moving funds between two CAPWALLETS under the same ownership and that holds the same currency is considered REALLOCATION.\n\n3.2.2. Moving funds between two CAPWALLETS under the same ownership, but which does not hold the same currency is considered CURRENCY EXCHANGE.\n\n3.2.3. Moving funds between two CAPWALLETS of different ownerships is considered a TRANSFER, regardless of the currencies the two wallets hold.\n\n3.3. Transfers happen in a currency of your choice, regardless of the currency held by the origin or destination wallet.\n\n3.3.1. If the origin wallet does not hold the same currency as the transfer’s currency, a currency exchange will take place.\n\n3.3.2. If the destination wallet does not hold the same currency as the transfer’s currency, a currency exchange will take place.\n\n3.4. Transactions are irreversible.\n\n3.4.1. If, by dispute or court decision, a transaction must be voided, this will be done through adding a movement on the involved wallets to nullify both the TRANSACTION and the COLLATERAL TRANSACTION.\n\n3.4.2. If you believe you have done a transaction by accident to the wrong destination, our support agents are unable to reverse it and is limited to providing you with an anonymous email address which you can use to attempt to contact the wallet owner.\n\n3.4.2.1. Replying to the contact attempt is entirely under the destination account owner’s choice.\n\n3.4.3. We are not responsible for resolving disputes or dispose any value due to chargeback claims. Use the PLATFORM at your risk.\n\n3.5. We charge a convenience fee for currency exchanges, and the final debited value is generally displayed before the action is performed. The fees and rates involved on your account usage is available at <[https://exchange.capitual.com](https://exchange.capitual.com)>.\n\n3.5.1. We are not responsible for losses due to price fluctuation of any currency.\n\n**ACCOUNT TOP-UP AND WITHDRAW**\n\n3.6. You may move funds from outside the PLATFORM to your account through supported crypto-currencies or one of the supported fiat currencies deposit methods.\n\n3.6.1. We reserve the right to charge a fee for processing deposits. We will not accept deposits if the fee value is higher than the processed deposit value.\n\n3.6.2. We reserve the right to calculate the minimum crypto-currency deposit blockchain confirmations required in order to have the funds available on your account, based on the risk score of the deposit transaction, which is based on the crypto-currency, origin wallet and value.\n\n3.6.3. We reserve the right not to process deposits if:\n\n - It comes from crypto-wallets and bank accounts that are known to have been involved in illegal activities;\n\n - The transaction currency is not the one held by the destination wallet.\n\n3.6.3.1. In some jurisdictions, we are not able to accept bank transfers from third-parties’ bank accounts to your CAPITUAL account. In this case, you will be warned about this on the deposits pages. Given insistence, we reserve the right not to process such transaction.\n\n3.6.3.2. Values which deposit have not been processed can be claimed by emailing <[support@capitual.com](mailto:support@capitual.com)>.\n\n3.6.3.3. We reserve the right to charge a variable fee over the chargeback of unprocessed deposit transactions.\n\n3.6.4. You confirm that you are permitted to use the financial instrument (i.e. bank account or crypto wallet) and you authorise CAPITUAL to charge the full amount of the deposit that you designate.\n\n3.6.4.1. You accept that we may collect such data (with no credentials) to internal use and this data may be shared with law enforcement agencies in case of suspicion of involvement with illegal activities.\n\n3.6.5. We reserve the right not to process a deposit transaction due to regulatory or compliance reasons, if we suspect the transaction may involve funds originated in illicit activities, including, but not limited to money laundering, terrorism financing, fraud or robbery.\n\n3.6.5.1. We are required, in such case, to terminate accounts that have presented with such behaviour, as well as reporting our suspicions to law enforcement agencies.\n\n3.7.1. We reserve the right to store values in offline storages. This is done as a security measure. For this reason, we might need to access assets stored offline. This may delay withdrawals operations.\n\n3.7.1.1. By no means we could grant that a requested amount will be available for withdrawing in our online storage and, therefore, we cannot guarantee that a requested withdrawal will not be delayed.\n\n3.7.1.2. We are not responsible for any issues originated by delayed transactions.\n\n3.7.2. We are not responsible by delays in transfers that involve external services, such as banks and wallet systems, regardless of the origin of the delay, which includes, but does not limit itself to holidays or low mining fees.\n\n3.7.3. We will not process transfers of any unsupported assets, or assets that have been moved to crypto-wallets that do not hold their currency.\n\n3.7.3.1. An unsupported currency is all and any currency which is not on the PLATFORM’s currency selector, visible, for example, when creating a new wallet.\n\n**ACCOUNT LIMITS**\n\n3.8. We make use of several calculation models and maintain a variable number of procedures and verifications about you to keep our members, partners and ourselves out of fraudulent activities.\n\n3.8.1. Your CAPITUAL account is imposed limits based on your activity.\n\n3.8.2. Your limits are available as soon as your account is confirmed, according to our KYC/AML Policy (accessible at <http://capgo.gq/kycaml>).\n\n3.8.3. Members who wish to have increased limits may make such request by email to <support@capitual.com>. We may require you additional documents as an enhanced due diligence takes place.\n\n3.8.3.1. We do not guarantee that a member will have increased limits, even after the enhanced due diligence procedure.\n\n**4. CAPITUAL IS NOT A BANK**\n\n4.1. CAPITUAL is not a banking institution.\n\n4.2. Held funds do not earn interest, except when informed.\n\n4.3. Balances are not insured by any insurance organisation, whether government-maintained or private.\n\n**5. INFORMATION SHARING**\n\n5.1. We do not share user’s information.\n\n5.1.1. We may share your information if we are requested by law enforcement agencies.\n\n5.1.1.1. Such disclosure is ruled by our Privacy Policy.\n\n5.2. We reserve the right to contact third-parties, including, but not limited to banking institutions, law enforcements, organizations and individuals and share with them details of any transaction, in case of suspicion of illegal behaviour.\n\n5.2.1. We reserve the right to contact individuals or organizations who may also be involved or be victim of any possible illegal activity that we may find among CAPITUAL users, regardless of the affected parties being or not CAPITUAL users.\n\n**6. NON-WAIVER OF RIGHTS**\n\n6.1. Our previous failure to take actions regarding to a suspicion or misbehavior by any CAPITUAL user does not waive our right to take actions to subsequent cases, regardless of the similarity between the cases. This agreement does not allow waiving any rights to any of the parts.\n\n**7. CAPITUAL DOES NOT ADVICE**\n\n7.1. CAPITUAL services, agents and communications are not meant to advice about any subject, including, but not limited to investing, legal matters or taxes.\n\n7.2. CAPITUAL does not provide tax advice. You are responsible for seeking information about any tax or legal implications that your use of our services may lead to.\n\n**8. RISKS INVOLVED IN USING CRYPTO-CURRENCIES**\n\n8.1. Crypto-currencies tend to be more volatile than fiat currencies, precious metals and other assets. Its price’s unpredictability may lead to huge losses over not a long time.\n\n8.2. Crypto-currencies are not backed by any government or institution, and tend not to maintain underlying assets.\n\n8.3. The use of crypto-currencies may be affected by government actions.\n\n8.4. It is not clear how the crypto-currencies prices are defined, but it may derive from the market movements towards buying and selling crypto-assets to and from fiat currencies.This creates potential for permanent total loss of values, should the market for a specified crypto-currency quits existing.\n\n8.5. The irreversibility nature of crypto-currencies creates field for online fraud schemes. We are unable to chargeback any crypto-currency transaction, and we do not have access to any crypto-wallet’s ownership. Losses resulting from fraud or accidental transfers are most likely unrecoverable.\n\n8.6. You agree that any dispute between you and product sellers or service providers has to be maintained in this scope, and shall not reach CAPITUAL’s scope.\n\n**9. THIRD-PARTY APPLICATIONS AND API USAGE**\n\n9.1. Our open API allows developers to create applications that run on CAPITUAL systems. CAPITUAL have no control over how these applications store your data and therefore CAPITUAL assumes no responsibility for the actions taken by these applications or their developers.\n\n9.1.1. You should choose responsibly whether to give an application or website access to your CAPITUAL account, and assume all the risks associated with the use of these applications.\n\n9.1.2. In case of dispute between you and the developers, 8.6 does apply.\n\n9.2. You may, at any time, remove permissions granted to third-parties’ applications from your account security settings (“Active Sessions”).\n\n**10. NO WARRANTY, LIMITED LIABILITY**\n\nWe work hard to maintain the service available, free of misfunctions and safe, but you use it at your sole risk. The service is provided on an\n\n**WE WORK HARD TO MAINTAIN THE SERVICE AVAILABLE, SAFE AND FREE OF MISFUNCTIONS, BUT THE USAGE OF THE SERVICE IS UNDER YOUR SOLE RISK. THE SERVICE IS PROVIDED ON AN “AS IS” AND “AS AVAILABLE” BASIS WITHOUT EXPRESS OR IMPLIED WARRANTY. WE DO NOT WARRANT THAT ACCESS TO EVERY PART OF THE SERVICE WILL BE, CONTINUOUSLY AND UNINTERRUPEDLY, TIMELY, ERROR-FREE OR COMPLETELY SECURE. SYSTEM’S OPERATION MAY BE INTERRUPTED BY NUMEROUS FACTORS THAT ARE OUTSIDE OF OUR CONTROL. WE MAKE NO WARRANTIES REGARDING THE REAL OR PERCEIVED VALUE OF ANY VIRTUAL CURRENCY OR TOKEN. THE PRICE OF THE VIRTUAL CURRENCIES IS UNPREDICTABLE, DESPITE WE BEING ABLE TO OFFER HISTORICAL DATA, AND WE TAKE NO RESPONSIBILITY FOR ANY LOSSES YOU MAY INCUR IN BY DEALING WITH VIRTUAL CURRENCIES, EVEN IF, FOR ANY REASON, THE LOSS WAS CAUSED BY A SERVICE DELAY, SUSPENSION OR INTERRUPTION. WE MAKE NO WARRANTY AS TO THE QUALITY, USEFULNESS, ACCURACY OR AVAILABILITY OF THE SERVICE.**\n\n**WE ARE NOT, IN ANY WAY, RESPONSIBLE FOR THE ACTIONS, CONTENT, INFORMATION OR DATA PROVIDED BY THIRD-PARTIES. CAPITUAL DIRECTORS, EMPLOYEES AND AGENTS, AFFILIATES, PARTNERS, SERVICE PROVIDERS OR ANY OF THEIR OFFICERS, DIRECTORS, EMPLOYEES OR REPRESENTATIVES, FROM ANY CLAIMS AND DAMAGES, IN ANY WAY CONNECTED WITH ANY CLAIM YOU HAVE AGAINST ANY SUCH THIRD-PARTIES.**\n\n**WE WILL NOT BE LIABLE TO YOU FOR ANY LOST PROFITS OR OTHER CONSEQUENTIAL, INDIRECT OR INCIDENTAL DAMAGES ARISING OUT OF OR IN CONNECTION WITH THIS AGREEMENT.**\n\n**WE RESERVE THE RIGHT AT ANY TIME TO MODIFY OR DISCONTINUE, TEMPORARILY OR PERMANENTLY, THE SERVICE (OR ANY PART THEREOF) WITH PRIOR NOTICE. YOU AGREE THAT CAPITUAL WILL NOT BE LIABLE TO YOU OR TO ANY THIRD-PARTY FOR ANY MODIFICATION, SUSPENSION OR DISCONTINUANCE OF THE SERVICE.**\n\n**SOME JURISDICTIONS DO NOT ALLOW THE EXCLUSION OF CERTAIN WARRANTIES, REPRESENTATIONS AND CONDITIONS OR THE LIMITATION OR EXCLUSION OF LIABILITY FOR INCIDENTAL OR CONSEQUENTIAL DAMAGES. ACCORDINGLY, SOME OF THE ABOVE LIMITATIONS MAY NOT APPLY TO YOU.**\n\n**11. ACCOUNT TERMINATION**\n\n11.1. If you want to stop using the PLATFORM, you can cancel your account. To do so, you are required to remove all the funds in your account and email [<support@capitual.com>](mailto:%3csupport@capitual.com%3e) telling your intents in terminating your account.\n\n11.1.1. You may request account re-opening at any moment, by sending an email to <support@capitual.com>. Your contact should be initiated by one of the emails added on your account, and you may be required to provide additional information about yourself or your account in order to have it re-opened.\n\n11.2. We may terminate, suspend or restrict your account if we suspect you are involved in any illegal activity or any of the Prohibited Businesses and Use (available at <http://capgo.gq/prohibited-businesses>), for violation of the present terms.\n\n11.2.1. If your account is terminated by the staff, you may request your funds following 3.1.6.3 and 3.1.6.4.\n\n**12. PREJUDICE MITIGATION AND COMPENSATION**\n\n12.1. You are responsible for all claims, fees, fines, penalties and/or any other liability incurred by Capitual Ltd, one or more of our corresponding companies or any third-party, caused by or arising out of your breach of the present document and/or your use of the PLATFORM.\n\n12.1. You agree to reimburse Capitual Ltd or a third-party for all and any such liability, fees and expenses incurred in the event which Capitual Ltd must enforce its rights hereunder.\n\n12.2. You agree that, if you violate the Prohibited Uses and Businesses (available at <http://capgo.gq/prohibited-businesses>) and your use generates prejudice to the PLATFORM or one or more of its users, partners or third-parties, a damage compensation value will be calculated, based on the prejudice proportions, and must be paid by you to Capitual Ltd.\n\n**13. LEGAL VALIDITY AND CONFLICTS**\n\n13.1. If any term or condition on the present document is considered by a court to be invalid, such term or condition will be changed and replaced, in order to accomplish the objectives of the original term of condition to the greatest similarity possible.\n\n13.2. If any term or condition on the present document conflicts with the laws of the jurisdictions that Capitual Ltd positions itself under, the latter will prevail.\n\n13.3. You agree that the English version of the Service, the present document and any other communication, legal document or page maintained by CAPITUAL will prevail in case of conflict with any translation, summary or shared copy.\n\n13.4. By registering to and confirming your CAPITUAL account, you are agreeing that you have read, completely understood and accepted all the terms and conditions on the present TERMS and PRIVACY POLICY. The latest version of any of these terms are always available at CAPITUAL’s website or may be requested by sending an email to <[legal@capitual.net](mailto:legal@capitual.net)>.\n"},"pt-BR":{"Pp":"**CAPITUAL PRIVACY POLICY, DATA STATEMENT AND COOKIE CONSENT FOR CAPITUAL LTD, CAPITUAL.COM WEBSITE AND CAPITUAL APPLICATION PROGRAMMING INTERFACE**\n\n**LAST UPDATED** MARCH 27TH 2019\n\nThis document rules how Capitual Ltd., corresponding and partners collect, use and shares your personal data received from you while you browse on Capitual website, mobile application or use Capitual API-powered applications and services. Hereinafter, the mentioned methods to access Capitual will be referred to as “PLATFORM”.\n\nIt’s important to make it clear that, in order to make the PLATFORM available, tasks performed by a variable number of companies is required. These companies may or may not be part of Capitual Group. All these companies have signed a non-disclosure agreement with Capitual Group and do follow this Privacy Policy while managing your data.\n\n**1. COLLECTED INFORMATION**\n\nWhile you use the PLATFORM, we collect information from and about you, from different sources. Part of the collected data may be provided by your environment (browser, computer, smartphone etc.); part of the collected data may come from external services and partners; part of the collected data, you are requested to provide.\n\n1.1. Our Account Confirmation procedure requires you to provide personal documents, including pictures. This information will be used to verify your identity and protect our users against fraud.\n\n1.1.1. Personal Documents may be shared with third-parties, including, but not limited to law enforcement agencies, in case of suspicion of involvement with illegal activities.\n\n1.2. Your profile settings require additional data, such as address and full name. This information is used, in conjunction with the data collected related in 1.1, during fraud investigations. This information may also be used during support procedures.\n\n1.3. We collect your IP address and we share your IP address, without any information that could relate the IP address to you, with third-party services that provide IP location. Such services provide us with your approximated geolocation coordinates (latitude and longitude) and political divisions (city, state, country, time zone etc.). This data is used to protect your account from unauthorized access.\n\n1.4. Every time you access the PLATFORM, we generate a unique identifier based on your environment (the “fingerprint”). This information is used to recognize your machine and protect your account against unauthorized access. This information may also be used during investigations.\n\n1.5. We may also collect your browsing patterns and log your navigation, in order to improve your experience on the PLATFORM.\n\n1.6. We collect and store information provided by your environment (browser or smartphone), which includes, but is not limited to user agent, browser and operating system version, screen resolution, language, time zone, CPU architecture and manufacturer. This information is used to improve your experience on the PLATFORM.\n\n1.7. We collect email addresses and phone numbers you provide. This contact information may be shared in case of suspicion of involvement with illegal activities with law enforcement agencies.\n\n**2. USAGE OF THE COLLECTED INFORMATION**\n\nThe collected information is used in one or more of the following manners:\n\n2.1. To properly offer the service. The collected data is essential for us to provide you with the features your Capitual account has access to.\n\n2.2. To improve your experience. We use analytic measures for a better understanding of our user’s behavior on our platform.\n\n2.3. To verify your identity. We use provided details as part of our Know-Your-Customer and Anti-Money Laundering procedures.\n\n2.4. To emit communications. We may send conditional communications to part of our users.\n\n2.5. Investigations. We analyze, through automated models and human verification, your account activity to protect our users against fraud, illegal activities or involvement with any of the Prohibited Uses and Businesses (available at <http://capgo.gq/prohibited-businesses>).\n\n2.6. Operations with service providers and partners. We may use your personal data for performing our partner’s KYC/AML procedures and other possible requirements.\n\n2.7. Third-party applications. If you give a Capitual API-powered application access to your account, we will share detailed information about your account and personal information, including the ability to take actions on your behalf. Their usage of your data is subject to their terms and privacy policy.\n\n**3. PROTECTING THE COLLECTED INFORMATION**\n\nWe maintain internal security measures to protect your personal information against unauthorized access, accidental change or loss.\n\n3.1. We maintain your personal information physically safe by safeguarding servers where we store user’s information and controlling access to it, allowing only those who need such data to fulfill their responsibilities.\n\n3.2. We maintain your personal information digitally safe by following security and network management standards, such as strong encryption (AES-256) of your data while travelling internally or to authorized third-parties, keeping network firewalls and intrusion detection systems.\n\n3.3. We limit access to your personal information to employees and third-parties, allowing their access only to the information set they need to have access to. These individuals and organizations have signed non-disclosure agreements with Capitual Ltd and are required to keep all the information confidential.\n\n3.4. Note that there are still security risks, as no organization can genuinely guarantee the lack of such risk. Bad actors may bypass even the most advanced security system.\n\n**4. SHARING OF THE COLLECTED INFORMATION**\n\nWe may share your information internally, between Capitual agents, or externally.\n\n4.1. We may share your data with service providers and partners, including, but not limited to banking institutions and payment services providers.\n\n4.2. We may share your data with law enforcement agencies, when requested by a court or if we suspect you are involved in illegal activities.\n\n4.3. We do not sell, trade or transfer your personal data to third-parties who are not involved in the process of keeping the feature set that you have access to, nor to those who have not signed a non-disclosure agreement with Capitual Ltd. In most cases, our partners have a limited access to your information, and in several situations, you are only referred to as a number or other non-personal identification.\n\n4.4. We may share a limited set of your personal data, including, but not limited to your browsing activities, environment (browser, operating system and device) information and IP address with analytic services providers.\n\n4.5. We may share your data in the event of transfer of ownership of Capitual Ltd or its corresponding companies, that may or may not be part of Capitual Group.\n\n4.6. We may share impersonal details about Capitual users as a whole, including, but not limited to the total transactions value on a certain timespan, number of users and our obligations. In this case, the provided data is never, in any way, related to any individual or organization.\n\n**5. JURISDICTION-SPECIFIC INQUIRIES**\n\n5.1. For European Union residents. We may share your personal data to parties outside European Union, including the United States. These parties include, but are not limited to analytic services providers, banking institutions and advertising agencies.\n\n5.1.1. By registering to a Capitual account, you consent  that we may transfer your data to third-parties, according to the chapter 4.\n\n5.1.2. According to the General Data Protection Regulation, Capitual Ltd is the controller and responsible for the PLATFORM and any personal information provided to the PLATFORM. In some situations, Capitual Ltd may act as data processor for third-parties’ entities that may have access to your personal information, as ruled by the chapter 4.\n\n5.2. For Brazil residents. According to the Law 12965/14, Article 7th X, you may request the total deletion of the data we have collected about you through email to <privacy@capitual.net>. In this case, we need of 30 (thirty) days to completely remove the information we have collected about you from the data storage systems of the companies of Capitual Group.\n\n**6. YOUR RIGHTS**\n\nYou are granted the rights, through sending email to <privacy@capitual.net>, to:\n\n6.1. Request a copy of the information we’ve collected about you.\n\n6.2. Request correction in incorrect data about you.\n\n6.3. Stop receiving our marketing communications.\n\n**7. RETAINING YOUR PERSONAL INFORMATION**\n\n7.1. We retain your Personal Information for as long as we need to perform the purposes the data was collected to.\n\n7.2. As soon as a specified information is no longer needed, it is securely removed from our records.\n\n7.3. If you want to stop receiving our market communications, we retain your contact details to make it possible to comply such request.\n\n7.4. In some situations when your data is accessed, it is done in an anonymized way, following 4.3.\n\n7.5. In some jurisdictions, we are required to store your Personal Information for a longer time, which is usually from some months to some years, after you request your account’s termination.\n\n**8. ASPECTS REGARDING CHILDREN PRIVACY**\n\n8.1. We do not collect or solicit information from minors (children under 16 years old).\n\n8.2. We do not allow individuals under 16 years old has registered to a Capitual account.\n\n8.2.1. As soon as it’s known that a specified account is being operated by a child under 16 years old, the account will be suspended.\n\n8.2.2. Accounts suspended due to the age of the owner may be requested to be reactivated as soon as the owner reaches 16 years old.\n\n**9. COOKIES POLICY**\n\n9.1. Cookies are small files that are placed on your device by the website or application you are using. Pixel tags, clear GIFs, pixels or web beacons are small blocks of code on a webpage or app that allow them to do things like reading and placing cookies and transmitting information to the PLATFORM or partners. Local Storage and Session Storage are technologies that provide access to a data storage on your device.\n\n9.2. We use these technologies for several reasons, which include the basic functionality of the PLATFORM, session keeping (logged-on state), service improvement and to help us to maintain your account’s security.\n\n9.3. We use third-party service providers that may make use of these technologies in order to provide analytical data, debugging information and performance data.\n\n9.4. We may store data in your mobile device when you make use of Capitual Mobile Application or Capitual Website.\n\n9.4.1. The stored information’s purpose is also included on 9.2.\n\n9.5. Like most websites, we may set cookies as soon as you start interacting with our platform.\n\n9.5.1. Your web browser sends cookies for a particular domain every time it connects to that domain. This means that we are able to read a specified cookie every time a new connection is made against one of our servers.\n\n9.5.2. As of Local Storage and Session Storage, your web browser does not send such data over the network, but we may set, capture and transmit it over the network, to our servers, at any time while you are navigating on the PLATFORM.\n\n9.6. Although we require that developers using our API be transparent on their usage of cookies and storage, your usage of third-parties’ applications that use Capitual API is entirely subject to their Privacy Policies.\n\n9.7. We may still set cookies on your browser even if you are not a Capitual member or you are not logged in.\n\n9.7.1. These cookies may be used by analytic services providers, in order to improve users’ experience on the PLATFORM.\n\n9.8. You may disable cookies on your browser, but the usage of the PLATFORM may be affected.\n\n**10. CHANGES TO THIS PRIVACY POLICY**\n\nWe may change this privacy policy at any time, without prior notification. The most up-to-date privacy policy may be obtained by emailing <legal@capitual.net>.\n\n**11. PRIVACY CONTACT**\n\nYou may send any privacy-related inquiries to us by sending an email to <privacy@capitual.net>.\n\nIf you reside in the European Union and is not satisfied with the resolution of your complaint, you may contact EU Data Protection Authority in your jurisdiction using the contact details provided at [http://ec.europa.eu/justice/article-29/structure/data-protection-authoritiesindex_en.htm](http://ec.europa.eu/justice/article-29/structure/data-protection-authoritiesindex_en.htm) for further assistance.\n","Tos":"**TERMOS DE USO PARA CAPITUAL LTD, PARA O SITE CAPITUAL.COM, PARA O APLICATIVO CAPITUAL, E OS APLICATIVOS TERCEIRIZADOS QUE UTILIZAM A PLATAFORMA DO CAPITUAL.**\n\nULTIMA ATUALIZAÇÃO EM 16 DE ABRIL DE 2019\n\n**1. INTRODUCÃO**\n\n\n1,1. O presente Acordo do Usuário, doravante referido como TERMOS, TERMOS ou TERMOS DE USO, rege a relação entre a Capitual Ltd., uma empresa sob as leis de Malta, registrada sob o número C 90611, autorizada pela Malta Financial Services Authority, com escritório registrado no Level 4, The Penthouse, Suíte 2, Ewropa Business Centre, Triq Dun Karm, Birkirkara BKR 9034, Malta, doravante referida como CAPITUAL, PLATAFORMA ou PLATAFORMA, NÓS ou NOSSA; e clientes dispostos a fazer uso dos serviços CAPITUAL, doravante referidos como CLIENTE, CONSUMIDOR, VOCÊ ou SEU.\n\n\n1,2. Contratantes. Este contrato representa um contrato entre o CLIENTE e a PLATAFORMA, assim como, quando aplicável, entre o CLIENTE e outros prestadores de serviços integrados ao CAPITUAL. Negar ou não seguir uma ou mais condições nos TERMOS pode levar ao encerramento da conta CAPITUAL DO CLIENTE e, se aplicável, a complicações legais adicionais, nos termos da lei.\n\n 1.2.1. Lei Aplicável. Você concorda que as leis da República de Malta, sem respeito aos princípios de leis conflitantes, regem o atual acordo entre as duas partes e deve regulamentar qualquer reclamação ou disputa que possa resultar da relação comercial que este documento cobre.\n\n1,3. Este acordo é intransferível. Qualquer tentativa de transferir o presente contrato será anulada e representa violação dos termos atuais.\n\n1,4. A CAPITUAL se reserva o direito de modificar, a qualquer momento, os presentes TERMOS, sem prévia notificação. Seu uso continuado da PLATAFORMA está sujeito aos TERMOS mais atualizados, que estão sempre disponíveis no site do CAPITUAL e também podem ser solicitados por e-mail para <[legal@capitual.net](mailto:legal@capitual.net)>.\n\n1,5. Se você está se registrando em nome de uma organização, está com isso confirmando que possui autorização legal para aceitar acordos em nome da sua organização.\n\n1,6. Se o CLIENTE não concordar com um ou mais de um termo ou condições nos presentes TERMOS ou POLÍTICA DE PRIVACIDADE, o CLIENTE não terá o direito de se inscrever para uma conta CAPITUAL e não utilizará nenhum serviço fornecido pelo CAPITUAL.\n\n**1.7. O CLIENTE RECONHECE A IMPORTÂNCIA DOS TERMOS AQUI PRESENTES E CONSIDEROU CADA TERMO CUIDADOSAMENTE AO TORNAR-SE MEMBRO DO SERVIÇO. ESTE DOCUMENTO CONTÉM DISPOSIÇÕES QUE DEFINEM AS POSSIBILIDADES E LIMITAÇÕES PARA REIVINDICAÇÕES QUE A PLATAFORMA E O CLIENTE PODERÃO TER. A RESPONSABILIDADE DO CAPITUAL AO CLIENTE TEM LIMITAÇÕES IMPOSTAS PELO PRESENTE ACORDO.**\n\n\n\n**2. COMPROMISSO QUE O CLIENTE FAZ PARA CONOSCO**\n\nVocê confirma e garante para nós que as seguintes declarações são e permanecerão verdadeiras enquanto você for um usuário CAPITUAL:\n\n2.1. Você tem pelo menos 18 anos de idade.\n \n2.2. Você é capaz de concordar com os termos atuais.\n\n**COMUNICADOS**\n\n2.2.1. Você concorda em receber e ler todas as comunicações oficiais emitidas pela Capitual, incluindo, mas não se limitando a:\n\n\n - Recibos ou rejeições de transações;\n\n - Alterações relativas aos presentes TERMOS, POLÍTICA DE PRIVACIDADE ou qualquer outro documento oficial;\n\n - Alterações relativas à PLATAFORMA e informações de manutenção;\n\n - Solicitando informações sobre você e/ou sua conta.\n\n\n2.2.2. Você concorda em responder, quando possível, a esses comunicados.\n\n2.2.3. Você concorda em manter seu e-mail primário atualizado (essa configuração é disponível na página da sua conta) e checar sua caixa de Spams para falsos-positivos envolvendo os comunicados do Capitual.\n\n2.2.4. Você checará se as informações recebidas são oficialmente publicadas pelo Capitual, e não um terceiro tentando se passar pelo Capitual.\n\n\n - Seu endereço de e-mail deve ser seu endereço de e-mail principal cadastrado no Capitual;\n\n - O e-mail não deve ter links para domínios ou subdomínios fora do CapituaL (ver 2.8.4) solicitando sua informação privada;\n\n - As questões relevantes para divulgação pública também devem estar disponíveis em nossa sala de imprensa, disponível em <[https://press.capitual.com](https://press.capitual.com)>;\n\n - Você pode sempre encaminhar um email para [support@capitual.com](mailto:support@capitual.com) se quiser verificar sua origem.\n\n2.2.5. Você pode, a qualquer momento e sem custos, solicitar outra cópia eletrônica de qualquer comunicação, em caso de perda acidental, enviando um e-mail para <[support@capitual.com](mailto:support@capitual.com)>.\n\n2.3. A lei da jurisdição onde você reside permite que você faça uso de todos os serviços CAPITUAL.\n\n2.4. Você não está envolvido e não se envolverá em nenhum Negócio Proibido.\n\n**DOCUMENTOS PESSOAIS E PROVA DE VIDA**\n\n2.5. Você nos fornecerá sua identidade oficial emitida pelo seu governo local (carteira de identidade ou carteira de motorista) ou um passaporte válido, e você concorda em fornecer novos documentos assim que os documentos fornecidos se tornarem inválidos ou reemitidos.\n\n2.5.1. Quando solicitado, você fornecerá informações pessoais adicionais e documentos adicionais, incluindo, mas não limitando-se a: nome legal, endereço, data de nascimento, informações bancárias, número de identificação do contribuinte, comprovante de endereço e imagens pessoais (“selfies”).\n\n2.5.2. Você concorda em nos fornecer dados de identificação e rastreamento de rosto, que serão usados para proteger VOCÊ e A PLATAFORMA.\n\n2.5.3. Você também executará, quando necessário, ações físicas, para nosso sistema de verificação automatizado de pessoas vivas ou por meio de chamada de vídeo com um agente autorizado.\n\n2.5.4. Você concorda que seus dados pessoais coletados podem ser compartilhados com terceiros, quando solicitados, incluindo, mas não se limitando à agências de aplicação da lei e bancos.\n\n2.5.5. Você concorda em manter suas informações de contato (e-mails e números de telefone) atualizadas e confirmar a propriedade de qualquer método de contato quando solicitado. Isso é feito por meio de um link ou de um token instantâneo enviado por email ou SMS.\n\n2.5.6. Nossos procedimentos de solicitar documentos são parte de nossa Política de conhecer nossos usuários e nossa política de prevenção contra lavagem de dinheiro, que pode ser visualizada em: <http://capgo.gq/kycaml>.\n\n**SANÇÕES**\n\n2.6. Você concorda em ter seus documentos e dados pessoais verificados em relação a listas de sanções como a Lista SDN da OFAC, Lista de Pessoas Negadas do Departamento de Comércio dos EUA, o regime de sanções financeiras do Tesouro Nacional e a Lista Consolidada de Sanções do Conselho de Segurança das Nações Unidas, diariamente.\n\n\n2.7. Você não fornecerá nenhum produto ou serviço a um residente de jurisdições sujeitas a embargos de Malta ou da União Europeia, disponíveis em <[http://capgo.gq/maltese-sanctions](http://capgo.gq/maltese-sanctions)> e em fontes oficiais.\n\n\n**INFORMAÇÕES DE SEGURANÇA**\n\n\n2.8. Você não usará sua conta CAPITUAL para praticar atividades ilegais contra a sociedade, organizações ou indivíduos. Você não acessará a conta CAPITUAL de outros usuários, enviará comunicações (e-mails, SMS ou qualquer outra forma de comunicação) semelhantes aos comunicados do CAPITUAL ou poderá levar os leitores a acreditar que se trata de um comunicado CAPITUAL oficial.\n\n2.8.1. Você não desenvolverá software, scripts, extensões de navegador ou aplicativos que roubam outras contas CAPITUAIS, fundos, senhas, códigos PIN ou informações pessoais de outra(s) pessoa(s).\n\n2.8.2. Você não fará nenhum tipo de teste de segurança, como testes de penetração, varredura de portas, S.I.G. (services information gathering) ou exploiting contra qualquer servidor CAPITUAL, sem autorização explícita emitida pela CAPITUAL.\n\n2.8.3. Você não fará nada que possa tornar os serviços do CAPITUAL indisponíveis ou diferentes para um ou mais usuários, incluindo, mas não se limitando a, negação de serviço ou negação de serviço distribuído.\n\n2.8.4. Se você encontrar qualquer vulnerabilidade em nossos sistemas, você entrará em contato imediatamente com <[security@capitual.net](mailto:security@capitual.net)> ou <[bugbounty@capitual.net](mailto:bugbounty@capitual.net)> , fornecendo uma prova de conceito (“PoC”), usando nosso ambiente de teste de rede (acessível através de <[https://my.testnet.capitual.com](https://my.testnet.capitual.com)> ) sempre que possível e, opcionalmente, juntando-se ao nosso programa de recompensas de bugs. Você não pode divulgar publicamente qualquer detalhe sobre tal vulnerabilidade antes da autorização explícita nem atacar qualquer conta net principal, mesmo que seja apenas para demonstrar a vulnerabilidade descoberta.\n\n2.8.5. Você não tentará reverter qualquer transação enviada ao CAPITUAL. Você não cobrará tais transações em seu banco nem tentará ataques de duplo investimento contra o CAPITUAL ou qualquer crypto moeda suportada pelo CAPITUAL.\n\n2.8.6. Você ajudará a manter sua conta CAPITUAL segura, o que envolve tarefas como, mas não limitadas a:\n\n - Usar uma senha segura e forte;\n\n - Alterar sua senha em uma base regular;\n\n - Não manter sua conta conectada em máquinas que você não está usando;\n\n - Não fazer cópias de sua senha, código PIN ou credenciais para seu endereço de e-mail principal, nem permitir o acesso de outras pessoas a essas informações.\n\n - Não copiar códigos de pedido de e-voucher, nem dar acesso público a ele, pois o comprovante de e-mail gera uma transação tratada de forma semelhante a qualquer outra transação, nos termos atuais e, especialmente, sobre sua irreversibilidade;\n\n - Não permitir que ninguém acesse seu telefone, mesmo bloqueado, já que seu telefone ou cartão SIM pode ser usado para redefinir sua senha e / ou código PIN;\n\n - Manter sua máquina segura, com um software antivírus decente e firewall atualizados;\n\n - Evitar páginas falsas, verificando sempre se a página que afirma ser da Capitual é o domínio principal ou um subdomínio de “[capitual.com](http://capitual.com)”, “[capitual.net](http : //capitual.net) \",\" [capitual.io](http://capitual.io) \",\" capitu.al \"ou\" capgo.gq \". Para verificar se você está em um desses domínios , verifique se, no URL em que você está, não há nada ou uma barra (“/“) seguida pelo caminho do URI após um dos domínios mencionados;\n\n - Não fornecer informações secretas ou credenciais de sua conta a ninguém, por e-mail, SMS, telefonema ou qualquer outro meio, mesmo aqueles que afirmam ser da Capitual. Nossos agentes possuem informações suficientes para fornecer suporte em nossos aplicativos internos, e nenhuma informação de conta deve ser solicitada ao usuário;\n\n - Evitar inserir suas credenciais (principalmente seu código PIN) em redes ou máquinas públicas, como cyber cafés, empresas, aeroportos, restaurantes ou redes de hotéis;\n\n - Usar códigos de login, emitidos nas configurações de segurança da sua conta, em vez do código de autenticação de dois fatores, ao acessar sua conta em redes desconhecidas, pois os tokens podem ser usados ​​apenas uma vez;\n\n - Ativar a autenticação de dois fatores;\n\n - Não colar nenhum código no console de desenvolvedores ou na barra de navegação do seu navegador.\n\n - Evitar clicar em links desconhecidos ou baixar anexos desconhecidos, mesmo aqueles de origens conhecidas;\n\n - Não dando códigos de login ou identificadores de sessão para ninguém.\n\n2.8.7. Você é totalmente responsável pelo estado da sua conta, bem como credenciais e acesso a ela.\n\n2.8.8. Você é totalmente responsável por todas as transações realizadas em nome da sua conta. Somente você é responsável por qualquer migração ou perda de fundos, inclusive resultantes de acesso autorizado. O CAPITUAL não se responsabiliza por perdas devido a contas com a segurança comprometida.\n\n2.9. Você recebe uma licença limitada, não exclusiva, intransferível e não sublicenciável para acessar e usar servidores CAPITUAL, incluindo nossas APIs. Você concorda que não tem direitos além dos limites dessa licença.\n\n2.9.1. Você não irá copiar, transmitir, distribuir, vender, revender, re-licenciar, descompilar, interceptar, fazer engenharia reversa, desmontar, modificar, corrigir, publicar, participar da criação, transferência ou venda de, criar trabalhos derivados, executar , exibir, incorporar em outro site (exceto através da API), ou de qualquer outra forma explorar o conteúdo de serviços CAPITUAL, em partes ou como um todo.\n\n2.9.2. Você não irá enquadrar, exibir, fazer Stream ou transmitir qualquer parte do SERVIÇO ou de nossas marcas comerciais sem prévia permissão explícita que pode ser solicitada por e-mail em <legal@capitual.net>.\n\n2.9.3. As transmissões de Streaming de nossas marcas registradas são regidos por nossos padrões de marca, que podem ser solicitados, após obter permissões explícitas, enviando um e-mail para <legal@capitual.net>.\n\n**SINGULARIDADES DE CONTAS**\n\n2.9. Você concorda que só manterá uma conta no CAPITUAL. Você pode solicitar o direito de ter uma conta adicional enviando um email para <[support@capitual.io](mailto:support@capitual.io)> a fim de obter uma permissão por escrito do CAPITUAL para manter essa segunda conta, mas nós não temos a obrigação de aprovar qualquer conta adjacente.\n\n2.9.1. Ter várias contas sem autorização do CAPITUAL pode levar ao encerramento das contas envolvidas.\n\n2.9.2. Uma conta do CAPITUAL não está sujeita a transferência para nenhuma pessoa ou organização.\n\n**DISPONIBILIDADE DE CONTA**\n\n2,10. Você confirma que possui ou tem acesso à configuração mínima de hardware e software necessária para acessar sua conta CAPITUAL, que é, a partir de agora, mas pode ser alterada no futuro:\n\n - Um computador funcional, com um sistema operacional instalado e funcionando corretamente;\n\n - Uma conexão de internet banda larga;\n\n - Um navegador da web atualizado, como o Google Chrome ou o Mozilla Firefox, com cookies e JavaScript autorizados;\n\n - Um endereço de email válido;\n\n - Espaço de armazenamento suficiente para os cookies e informações armazenadas em cache (pelo menos 20 megabytes, 50 megabytes recomendados); \n\ne também, mas opcionalmente: \n\n - Um smartphone executando versões recentes do Android ou iOS, para executar o Aplicativo Móvel Capitual.\n\n**3. DISPOSIÇÕES DA PLATAFORMA**\n\n3.1. A CAPITUAL fornece ao CLIENTE uma CONTA CAPITUAL, que é uma área pessoal com funções baseadas nos dados pessoais do usuário.\n\n3.1.1. Uma CONTA CAPITUAL detém diferentes formas de fundos, tais como cripto moedas, metais preciosos ou tokens internos, que podem ser trocados de e para moedas criptografadas e fiat dentro da PLATAFORMA.\n\n**ATIVOS COMO TOKENS**\n\n3.1.2. A PLATAFORMA fornece espaços na forma de “carteiras”, doravante denominados “CAPWALLETS”.\n\n3.1.3. CAPWALLETS detêm ativos (\"tokens\") que são financeiramente equivalentes aos ativos que você coloca na PLATAFORMA.\n\n3.1.3.1. Seus direitos são exclusivamente sobre o valor dos tokens que você detém na PLATAFORMA em qualquer um dos métodos de saque suportados para sua jurisdição.\n\n3.1.4. Os ativos colocados na PLATAFORMA serão transferidos para a CAPITUAL ou uma das empresas correspondentes autorizadas em todo o mundo.\n\n3.1.5. Para colocar recursos na PLATAFORMA, você receberá o valor econômico dos ativos na forma de tokens, na CAPWALLET da sua escolha. O valor dos seus tokens pode ser exibido, em tempo real, na moeda virtual suportada (doravante referida como \"MOEDA\") de sua escolha, e também pode ser trocado, em partes ou inteiramente, por qualquer  outra moeda suportada.\n\n3.1.6. No caso de sua conta ser considerada inativa (ou seja, pelo não uso da PLATAFORMA por mais de 180 dias ou o tempo definido pelo seu estado), o CAPITUAL poderá ser obrigado a declarar seus fundos como propriedade não reclamada ao governo da jurisdição em que a conta está registrada.\n\n3.1.6.1. De antemão, a CAPITUAL tentará vários métodos para contatá-lo, o que inclui, mas não se limita ao envio de e-mail, contato por telefone ou endereço físico. Essas tentativas ocorrerão alguns dias antes do período de 90 dias em que sua conta não recebeu acesso. Se, no entanto, nenhuma tentativa alcançar o sucesso, seus fundos retidos podem ser entregues à jurisdição.\n\n3.1.6.2. Uma conta pode ser marcada como inativa caso nenhum acesso seja registrado nela após 180 dias (ou o horário definido pelo seu estado). Um acesso é considerado usando uma sessão criada anteriormente ou iniciando uma nova sessão.\n\n3.1.6.3. Se sua conta for encerrada, você pode solicitar o envio do saldo restante pelo correio. Nesse caso, ele será enviado para o endereço definido nas configurações da sua conta.\n\n3.1.6.4. Se seus fundos foram bloqueados, total ou parcialmente, por agências de aplicação da lei, não poderemos atender a essa solicitação.\n\n3.1.7. Contamos com terceiros para armazenar parte dos fundos e fornecer os serviços de câmbio. Como todo o mercado de criptografia é novo, alguns desses terceiros não possuem um longo histórico operacional e, portanto, seu gerenciamento não é comprovado. Uma vez que o mercado de criptografia e o histórico de serviços financeiros têm alguns exemplos de perda de fundos devido a fraude, roubo ou outras razões, você aceita o risco de que uma ou mais dessas partes não cumpram estritamente os nossos acordos com eles.\n\n**TRANSFERÊNCIAS E TROCA**\n\n3.2. Você pode transferir fundos de um CAPWALLET para outra, desde que a CAPWALLET de origem esteja sob sua propriedade ou você reserve os direitos para gastá-lo. Este ato resulta em uma TRANSAÇÃO (transação subtraindo o valor da carteira de origem) e uma TRANSAÇÃO COLATERAL (transação adicionando o valor para a carteira de destino).\n\n3.2.1. Mover fundos entre duas CAPWALLETS sob a mesma propriedade e que detém a mesma moeda é considerado RELOCAÇÃO.\n\n3.2.2. A movimentação de fundos entre duas CAPWALLETS sob a mesma propriedade, mas que não possuem a mesma moeda, é considerada TROCA DE MOEDAS.\n\n3.2.3. Mover fundos entre duas CAPWALLETS de diferentes proprietários é considerado uma TRANSFERÊNCIA, independentemente das moedas que as duas carteiras operam.\n\n3.3. As transferências acontecem em uma moeda de sua escolha, independentemente da moeda mantida pela carteira de origem ou de destino.\n\n3.3.1. Se a carteira de origem não tiver a mesma moeda que a moeda da transferência, será efetuada uma troca de moeda.\n\n3.3.2. Se a carteira de destino não tiver a mesma moeda que a moeda da transferência, será efetuada uma troca de moeda.\n\n3.4. As transações são irreversíveis.\n\n3.4.1. Se, por disputa ou decisão judicial, uma transação tiver que ser anulada, isso será feito através da adição de um movimento nas carteiras envolvidas para anular tanto a TRANSAÇÃO como a TRANSAÇÃO COLATERAL.\n\n3.4.2. Se você acredita que acidentalmente fez uma transação para o destinatário errado, nossos agentes de suporte não podem revertê-la e está limitado a fornecer um endereço de e-mail anônimo para que você possa tentar para entrar em contato com o proprietário da carteira.\n\n3.4.2.1. A resposta à tentativa de contato é totalmente feita de acordo com a escolha do proprietário da conta de destino.\n\n3.4.3. Não nos responsabilizamos por resolver disputas ou descartar qualquer valor devido a reivindicações de estorno. Use a PLATAFORMA por sua conta e risco.\n\n3.5. Cobramos uma taxa de conveniência para trocas de moeda e o valor final debitado geralmente é exibido antes que a ação seja executada. As taxas e tarifas envolvidas no uso da sua conta estão disponíveis em <[https://exchange.capitual.com](https://exchange.capitual.com)>.\n\n3.5.1. Nós não somos responsáveis ​​por perdas devido à flutuação nos preços de qualquer moeda.\n\n**COMPLETAR CONTA E SAQUES**\n\n3.6. Você pode transferir fundos de fora da PLATAFORMA para sua conta através de criptografias suportadas ou de um dos métodos de depósito de moedas fiduciárias suportados.\n\n3.6.1. Nós nos reservamos o direito de cobrar uma taxa pelo processamento de depósitos. Não aceitaremos depósitos se o valor da taxa for maior que o valor do depósito processado.\n\n3.6.2. Reservamo-nos o direito de calcular as confirmações mínimas de blockchain de depósito criptografado necessárias para ter os fundos disponíveis em sua conta, com base na pontuação de risco da transação de depósito, que é baseada na moeda criptografada, na carteira e no valor de origem.\n\n3.6.3. Nós reservamos o direito de não processar depósitos caso:\n\n - Provém de cripto-carteiras e contas bancárias que se sabe terem estado envolvidas em atividades ilegais;\n\n - A moeda da transação não é aquela mantida pela carteira de destino.\n\n3.6.3.1. Em algumas jurisdições, não podemos aceitar transferências bancárias de contas bancárias de terceiros para sua conta CAPITUAL. Neste caso, você será avisado sobre isso nas páginas de depósitos. Dada a insistência, nos reservamos o direito de não processar tal transação.\n\n3.6.3.2. Os valores cujo depósito não foi processado podem ser solicitados por e-mail <[support@capitual.com](mailto:support@capitual.com)>.\n\n3.6.3.3. Reservamo-nos o direito de cobrar uma taxa variável sobre o estorno de transações de depósito não processadas.\n\n3.6.4. Você confirma que tem permissão para usar o instrumento financeiro (por exemplo, conta bancária ou carteira criptográfica) e autoriza a CAPITUAL a cobrar o valor total do depósito que você designar.\n\n3.6.4.1. Você aceita que possamos coletar tais dados (sem credenciais) para uso interno e esses dados podem ser compartilhados com as agências de aplicação da lei em caso de suspeita de envolvimento com atividades ilegais.\n\n3.6.5. Reservamo-nos o direito de não processar uma transação de depósito devido a razões regulatórias ou de conformidade, se suspeitarmos que a transação possa envolver fundos originados de atividades ilícitas, incluindo, mas não limitado a, lavagem de dinheiro, financiamento do terrorismo, fraude ou roubo.\n\n3.6.5.1. Somos obrigados, nesse caso, a encerrar contas que apresentaram tal comportamento, bem como relatar nossas suspeitas a agências de aplicação da lei.\n\n3.7.1. Nós nos reservamos o direito de armazenar valores em armazéns offline. Isso é feito como uma medida de segurança. Por esse motivo, talvez precisemos acessar os ativos armazenados off-line. Isso pode atrasar as operações de retirada.\n\n3.7.1.1. De maneira alguma poderíamos garantir que uma quantia solicitada estará disponível para retirada em nosso armazenamento online e, portanto, não podemos garantir que uma retirada solicitada não seja atrasada.\n\n3.7.1.2. Não nos responsabilizamos por quaisquer problemas originados por transações atrasadas.\n\n3.6.4. Você confirma que tem permissão para usar o instrumento financeiro (por exemplo, conta bancária ou crypto wallet) e autoriza a CAPITUAL a cobrar o valor total do depósito que você designar.\n\n3.6.4.1. Você aceita que podemos coletar tais dados (sem credenciais) para uso interno e esses dados podem ser compartilhados com as agências de aplicação da lei em caso de suspeita de envolvimento com atividades ilegais.\n\n3.6.5. Reservamo-nos o direito de não processar uma transação de depósito devido a razões regulatórias ou de conformidade, se suspeitarmos que a transação pode envolver fundos originados de atividades ilícitas, incluindo, mas não se limitando a, lavagem de dinheiro, financiamento do terrorismo, fraude ou roubo.\n\n3.6.5.1. Somos obrigados, nesse caso, a encerrar contas que apresentaram tal comportamento, bem como relatar nossas suspeitas à agências de aplicação da lei competentes.\n\n3.7.1. Nós nos reservamos o direito de armazenar valores em armazéns offline. Isso é feito como uma medida de segurança. Por esse motivo, talvez precisemos acessar os ativos armazenados off-line. Isso pode atrasar as operações de SAQUE.\n\n3.7.1.1. De maneira alguma poderíamos garantir que uma quantia solicitada estará disponível para Saque em nosso armazenamento online e, portanto, não podemos garantir que um saque solicitado não seja atrasado.\n\n3.7.1.2. Não nos responsabilizamos por quaisquer problemas originados pelo atraso nas transações.\n\n3.7.2. Não nos responsabilizamos por atrasos em transferências que envolvam serviços externos, como bancos e sistemas de carteira, independentemente da origem do atraso, o que inclui, mas não se limita a feriados ou baixas taxas de mineração.\n\n3.7.3. Não processaremos transferências de quaisquer ativos não suportados ou ativos que tenham sido movidos para cripto-carteiras que não mantenham sua moeda.\n\n3.7.3.1. Uma moeda não suportada é toda e qualquer moeda que não esteja no seletor de moedas da PLATAFORMA, visível, por exemplo, ao criar uma nova carteira.\n\n**LIMITES DE CONTA**\n\n3.8. Fazemos uso de vários modelos de cálculo e mantemos um número variável de procedimentos e verificações sobre você para manter nossos membros, parceiros e nós mesmos fora de atividades fraudulentas.\n\n3.8.1. Sua conta CAPITUAL tem limites designados com base em sua atividade.\n\n3.8.2. Seus limites estarão disponíveis assim que sua conta for confirmada, de acordo com nossa Política KYC/AML (acessível em <http://capgo.gq/kycaml>).\n\n3.8.3. Os membros que desejarem aumentar os limites podem fazer tal solicitação por e-mail para <support@capitual.com>. Podemos exigir documentos adicionais à medida que uma diligência prévia ocorrer.\n\n3.8.3.1. Não garantimos que um membro tenha limites maiores, mesmo após o procedimento de Diligência prévia.\n\n**4. CAPITUAL NÃO É UM BANCO**\n\n4.1. O CAPITUAL não é uma instituição bancária.\n\n4.2. Os fundos guardados não geram juros, exceto quando informados.\n\n4.3. Os saldos não são segurados por nenhuma seguradora, seja mantida pelo governo ou privada.\n\n**5. COMPARTILHAMENTO DE INFORMAÇÕES**\n\n5.1. Nós não compartilhamos informações do usuário.\n\n5.1.1. Podemos compartilhar suas informações se formos solicitados por agências competentes, por lei.\n\n5.1.1.1. Essa divulgação é regida pela nossa Política de Privacidade.\n\n5.2. Reservamo-nos o direito de entrar em contato com terceiros, incluindo, mas não limitado a, instituições bancárias, autoridades, organizações e indivíduos e compartilhar com eles detalhes de qualquer transação, em caso de suspeita de comportamento ilegal.\n\n5.2.1. Reservamo-nos o direito de entrar em contato com indivíduos ou organizações que também possam estar envolvidos ou serem vítimas de qualquer atividade ilegal possível que possamos encontrar entre os usuários do CAPITUAL, independentemente de as partes afetadas serem ou não usuários do CAPITUAL.\n\n**6. NÃO RENÚNCIA DE DIREITOS**\n\n6.1. Nossa falha anterior em tomar ações com relação a uma suspeita ou mau comportamento por parte de qualquer usuário do CAPITUAL não renuncia ao nosso direito de tomar ações para casos subseqüentes, independentemente da similaridade entre os casos. Este acordo não permite a renúncia de quaisquer direitos a qualquer uma das partes.\n\n**7. CAPITUAL NÃO ACONSELHA**\n\n7.1. Os serviços, agentes e comunicações da CAPITUAL não se destinam a aconselhar sobre qualquer assunto, incluindo, entre outros, investimentos, questões legais ou impostos.\n\n7.2. A CAPITUAL não fornece aconselhamento fiscal. Você é responsável por buscar informações sobre quaisquer implicações fiscais ou jurídicas que seu uso de nossos serviços possa causar.\n\n**8. RISCOS ENVOLVIDOS NA UTILIZAÇÃO DE MOEDAS DE CRIPTO**\n\n8.1. Cripto moedas tendem a ser mais voláteis do que moedas fiduciárias, metais preciosos e outros ativos. A imprevisibilidade do preço pode levar a enormes perdas em pouco tempo.\n\n8.2 As cripto moedas não são apoiadas por nenhum governo ou instituição e tendem a não manter ativos subjacentes.\n\n8.3. O uso de crypto moedas pode ser afetado por ações do governo.\n\n8.4. Não está claro como os preços das crypto moedas são definidos, mas pode derivar dos movimentos do mercado em direção à compra e venda de ativos criptográficos para e de moedas fiduciárias. Isso cria potencial para perda total permanente de valores, se o mercado para uma determinada Crypto deixar de existir.\n\n8.5. A natureza irreversível das cripto moedas cria um campo para esquemas de fraudes on-line. Não podemos externar nenhuma transação de crypto moedas e não temos acesso à propriedade de qualquer carteira de criptografia. Perdas resultantes de fraude ou transferências acidentais são provavelmente irrecuperáveis.\n\n8.6. Você concorda que qualquer disputa entre VOCÊ e vendedores de produtos ou prestadores de serviços deve ser mantida neste âmbito e não deve atingir o âmbito do CAPITUAL.\n\n**9. APLICAÇÕES DE TERCEIROS E USO DA API**\n\n9.1. Nossa API aberta permite que os desenvolvedores criem aplicativos executados nos sistemas do CAPITUAL. O CAPITUAL não tem controle sobre como esses aplicativos armazenam seus dados e, portanto, O CAPITUAL não assume nenhuma responsabilidade pelas ações tomadas por esses aplicativos ou seus desenvolvedores.\n\n9.1.1. Você deve escolher responsavelmente se deve dar a um aplicativo ou site acesso à sua conta CAPITUAL e assumir todos os riscos associados ao uso desses aplicativos.\n\n9.1.2. Em caso de disputa entre você e os desenvolvedores da API, o item 8.6 se aplica.\n\n9.2. Você pode, a qualquer momento, remover as permissões concedidas a aplicativos de terceiros das configurações de segurança da sua conta (\"Sessões ativas\").\n\n**10. SEM GARANTIA, RESPONSABILIDADE LIMITADA**\n\nNós concentramos esforços para manter o serviço disponível, livre de falhas e segurança, mas você o utiliza por sua conta e risco. O serviço é prestado em\n\n**NÓS CONCENTRAMOS ESFORÇOS PARA MANTER O SERVIÇO DISPONÍVEL, SEGURO E FUNCIONANDO CORRETAMENTE, MAS O USO DO SERVIÇO ESTÁ SOBRE SEU PRÓPRIO RISCO. O SERVIÇO É FORNECIDO “COMO ESTÁ” E “CONFORME DISPONÍVEL” SEM GARANTIA EXPRESSA OU IMPLÍCITA. NÓS NÃO GARANTIMOS QUE O ACESSO A CADA PARTE DO SERVIÇO SERÁ CONTINUAMENTE, ININTERRUPTAMENTE, SEM ERROS OU COMPLETAMENTE SEGURO. A OPERAÇÃO DO SISTEMA PODE SER INTERROMPIDA POR FATORES NUMEROSOS QUE ESTÃO FORA DO NOSSO CONTROLE. NÓS NÃO FAZEMOS NENHUMA GARANTIA RELATIVA AO VALOR REAL OU PERCEBIDO DE QUALQUER MOEDA VIRTUAL OU TOKEN. O PREÇO DAS MOEDAS VIRTUAIS É IMPREVISÍVEL,POR MAIS QUE NÓS POSSAMOS OFERECER INFORMAÇÕES HISTÓRICAS, E NÃO TOMAREMOS NENHUMA RESPONSABILIDADE POR QUAISQUER PERDAS QUE VOCÊ PODE OCORRER LIDANDO COM MOEDAS VIRTUAIS, MESMO SE, POR QUALQUER MOTIVO, A PERDA FOI CAUSADA POR UM ATRASO, SUSPENSÃO OU INTERRUPÇÃO DO SERVIÇO. NÃO DAMOS GARANTIA QUANTO À QUALIDADE, UTILIDADE, PRECISÃO OU DISPONIBILIDADE DO SERVIÇO.**\n\n**NÃO SOMOS, DE QUALQUER FORMA RESPONSÁVEIS PELAS AÇÕES, CONTEÚDO, INFORMAÇÕES OU DADOS FORNECIDOS POR TERCEIROS. DIRETORES, FUNCIONÁRIOS E AGENTES, AFILIADOS, PARCEIROS, FORNECEDORES DE SERVIÇOS OU QUALQUER DE SEUS DIRETORES, DIRETORES, EMPREGADOS OU REPRESENTANTES, DE QUALQUER REIVINDICAÇÃO E DANO, DE QUALQUER FORMA RELACIONADOS COM QUALQUER REIVINDICAÇÃO CONTRA SUAS TERCEIRAS.**\n\n**NÓS NÃO SEREMOS RESPONSÁVEIS POR VOCÊ POR LUCROS CESSANTES OU OUTROS DANOS CONSEQÜENTES, INDIRETOS OU INCIDENTAIS DECORRENTES OU RELACIONADOS A ESTE CONTRATO.**\n\n**RESERVAMOS O DIREITO A QUALQUER MOMENTO DE MODIFICAR OU INTERROMPER, TEMPORARIAMENTE OU PERMANENTEMENTE, O SERVIÇO (OU QUALQUER PARTE DO MESMO) COM AVISO PRÉVIO. VOCÊ CONCORDA QUE A CAPITUAL NÃO SERÁ RESPONSÁVEL POR VOCÊ OU POR TERCEIROS POR QUALQUER MODIFICAÇÃO, SUSPENSÃO OU DESCONTINUAÇÃO DO SERVIÇO.**\n\n**ALGUMAS JURISDIÇÕES NÃO PERMITEM A EXCLUSÃO DE DETERMINADAS GARANTIAS, REPRESENTAÇÕES E CONDIÇÕES OU A LIMITAÇÃO OU EXCLUSÃO DE RESPONSABILIDADE POR DANOS INCIDENTAIS OU CONSEQÜENCIAIS. DE ACORDO, ALGUMAS DAS LIMITAÇÕES ACIMA PODEM NÃO SE APLICAR A VOCÊ.**\n\n**11. RESCISÃO DA CONTA**\n\n11,1. Se você quiser parar de usar a PLATAFORMA, você pode cancelar sua conta. Para fazer isso, você deve remover todos os fundos da sua conta e enviar um e-mail [<support@capitual.com>](mailto:%3csupport@capitual.com%3e) informando suas intenções para encerrar sua conta.\n\n11.1.1. Você pode solicitar a reabertura de conta a qualquer momento, enviando um email para <support@capitual.com>. Seu contato deve ser iniciado por um dos e-mails adicionados em sua conta, e você pode ser solicitado a fornecer informações adicionais sobre você ou sua conta para que seja reaberto.\n\n11,2. Podemos rescindir, suspender ou restringir sua conta se suspeitarmos que você esteja envolvido em qualquer atividade ilegal ou em qualquer um dos Negócios e Uso Proibidos (disponível em <http://capgo.gq/prohibited-businesses>), por violação dos presentes termos.\n\n11.2.1. Se sua conta for encerrada pela equipe, você pode solicitar seus fundos seguindo o 3.1.\n\n11.2.1. Se sua conta for encerrada pela equipe, você pode solicitar seus fundos seguindo os itens 3.1.6.3 e 3.1.6.4.\n\n**12. MITIGAÇÃO DE E COMPENSAÇÃO DE PERDAS**\n\n12,1. Você é responsável por todas as reclamações, taxas, multas, penalidades e / ou qualquer outra responsabilidade incorrida pela Capitual Ltd, uma ou mais de nossas empresas correspondentes ou de terceiros, causada por ou decorrente de sua violação do presente documento e / ou ou o seu uso da PLATAFORMA.\n\n12,1. Você concorda em reembolsar a Capitual Ltd ou um terceiro por toda e qualquer responsabilidade, taxas e despesas incorridas no caso em que a Capitual Ltd deva fazer valer seus direitos.\n\n12,2. Você concorda que, se violar os Usos e Negócios Proibidos (disponíveis em <http://capgo.gq/prohibited-businesses>) e seu uso gerar prejuízo para a PLATAFORMA ou um ou mais de seus usuários, parceiros ou terceiros , um valor de compensação de danos será calculado, com base nas proporções do prejuízo, e deve ser pago por você à Capitual Ltd.\n\n**13. VALIDADE LEGAL E CONFLITOS**\n\n13.1. Se qualquer termo ou condição do presente documento for considerado inválido por um tribunal, tal termo ou condição será alterado e substituído, a fim de cumprir os objetivos do termo original de acordo com a maior semelhança possível.\n\n13,2. Se qualquer termo ou condição no presente documento conflitar com as leis das jurisdições nas quais a Capitual Ltd. se posiciona, o último prevalecerá.\n\n13,3. Você concorda que a versão em inglês do Serviço, o presente documento e qualquer outra comunicação, documento legal ou página mantida pela CAPITUAL prevalecerá em caso de conflito com qualquer tradução, resumo ou cópia compartilhada.\n\n13.4. Ao se registrar e confirmar sua conta CAPITUAL, você concorda que leu, compreendeu completamente e aceitou todos os termos e condições dos TERMOS e POLÍTICA DE PRIVACIDADE atuais. A versão mais recente de qualquer um desses termos está sempre disponível no site da CAPITUAL ou pode ser solicitada enviando um e-mail para <[legal@capitual.net](mailto:legal@capitual.net)>.\n\n"}};

