// @preval

const fs = require('fs')

let files = {}, lang

fs.readdirSync(__dirname + '/../i18n').forEach(file => {
	if (file.endsWith("_tos.md") || file.endsWith("_pp.md")) {
		lang = file.substr(0, 5)
		if (!files.hasOwnProperty(lang)) files[lang] = {}
		files[lang][file.endsWith("_tos.md") ? "Tos" : "Pp"] = fs.readFileSync(__dirname + '/../i18n/' + file).toString()
	}
})

module.exports = files


